@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap');
*
{
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: 'poppins', sans-serif;
}
.section{
position: relative;
width: 100%;
display: flex;
background: linear-gradient(45deg, #ff0c9e, #a624f7f0);
}

.section .imgBx
{
position: relative;
width: 50%;
height: 100%;
}
.section .imgBx::before
{
content: '';
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
background: linear-gradient(225deg,#e91e63,#03a9f4);
z-index: 1;
mix-blend-mode: screen;
}
.section .imgBx img{
position: relative;
top: 0;
left: 0;
width: 100%;
height: 100%;
object-fit: cover;
}
.section .contentBx .formBx .inputBx{
    margin-bottom: 10px;
    
    }
.section .contentBx
{
display: flex;
justify-content: center;
align-items: center;
width: 50%;
height: 100%;
background: linear-gradient(45deg, #ff0c9e, #a624f7f0);

}
.section .contentBx .formBx{
width: 50%;
}

.section .contentBx .formBx h2
{
    color: #3de8f9;
    font-weight: 400;
    font-size: 1.5em;
    text-transform: uppercase;
    margin: 20px;
    border: 6px solid #da346c;
    border-radius: 29%;
    display: table;
    letter-spacing: 1px;
}

.section .contentBx .formBx .inputBx span{
margin-bottom: 5px;
font-size: 16px;
display: inline-block;
color: #607d8b;
font-weight: 300;
font-size: 16px;
letter-spacing: 1px;
width: 100%;

}
.section .contentBx .formBx .inputBx input{
    width: 100%;
    padding: 15px 18px;
    outline: none;
    font-weight: 100;
    border: 1px solid #25b8ff;
    font-size: 16px;
    letter-spacing: 1px;
    color: #000;
    background-color: #f7dbf0;
    border-radius: 30px;
}
.section .contentBx .formBx .inputBx input[type="button"]{
background: #ff4584;
color: #fff;
outline: none;
border: none;
font-weight: 500;
cursor: pointer;
}
.section .contentBx .formBx .inputBx input[type="button"]:hover
{
background: #f53677;

}
.section .contentBx .formBx .remember{
margin-bottom: 10px;
color: #fff;
font-weight: 400;
font-size: 14px;
}
.section .contentBx .formBx label{
color: #37fdeb;;
font-size: 22px;
}
.section .contentBx .formBx .inputBx p{
color: #000;
font-size: large;
white-space: nowrap;
}
.section .contentBx .formBx .inputBx p a{
    color: #d8ff03;
    font-size: 23px;
}
.section .contentBx .formBx h3{
    color: #2be2ff;
    text-align: center;
    margin: 53px 0 10px;
    font-weight: 500;
    font-size: 25px;
}
.section .contentBx .formBx .sci{
display: flex;
justify-content: center;
align-items: center;
}
.section .contentBx .formBx .sci li{
list-style: none;
width: 50px;
height: 50px;
display: flex;
justify-content: center;
align-items: center;
background: #607d8b;
border-radius: 50%;
margin: 0 7px;
}
.section .contentBx .formBx .sci li:hover{
background: #ff4584;
}
.section .contentBx .formBx .sci li img{
transform: scale(0.9);
filter: invert(1);
}
.section .contentBx .formBx .sci li img:hover{
transform: scale(1.2);
cursor: pointer;
filter: invert(0);
}
@media (max-width: 1000px){
.section .imgBx
{
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
} 
.section .contentBx{
margin-top: 20px;
display: flex;
justify-content: center;
align-items: center;
width: 100%;
height: 100%;
z-index: 1;
}
.section .contentBx .formBx{
width: 100%;
padding: 20px;
background: rgb(255, 255, 255);
margin: 50px;
border-radius: 50px;
}
.section .contentBx .formBx h3{
color: #607d8b;
text-align: center;
margin: 30px 0 10px;
font-weight: 500;
}
.section .contentBx .formBx h2
{
    color: #1694a0;

}
.section .contentBx .formBx .inputBx p{
    color: #000;
    white-space: nowrap;
}
}