.body{
    margin-top: 30px;
    display: flex;
    position: relative;
    width: 300px;
    justify-content: center;
    align-items: center;
    background:linear-gradient(
        45deg
        , #35f6ff, #ff3f3fd9);
    width: max-content;
}
.clock{
    width: 300px;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('../media/static/clock.png');
    background-size: cover;
    /* border: 4px solid #091921;
    border-radius: 50%; */
    box-shadow: 0 -15px 15px rgb(62 174 253 / 88%), inset 0px -15px 15px rgb(155 104 187 / 62%), 0 8px 13px rgb(133 40 191 / 81%), inset 0 0px 13px rgb(162 247 172 / 65%)
}
.clock::before{
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    background: #fff;
    border-radius: 50%;
    z-index: 10;
}
.clock .hour, .clock .min, .clock .sec{
    position: absolute;
}
.clock .hour, .hr{
    width: 163px;
    height: 163px;
}
.clock .min, .mn{
    width: 226px;
    height: 226px;
}
.clock .sec, .sc{
    width: 251px;
    height: 251px;
}
.hr, .mn, .sc{
    display: flex;
    justify-content: center;
    position: absolute;
    border-radius: 50%
}
.hr::before{
    content: '';
    position: absolute;
    width: 8px;
    height: 76px;
    background: #ff105e;
    z-index: 5;
    border-radius: 6px 6px 0 0;
}
.mn::before{
    content: '';
    position: absolute;
    width: 8px;
    height: 110px;
    background: #ebdce1;
    z-index: 6;
    border-radius: 6px 6px 0 0;
}
.sc::before{
    content: '';
    position: absolute;
    width: 3px;
    height: 125px;
    background: #2932ad;
    z-index: 7;
    border-radius: 6px 6px 0 0;
}