.faqcontainer{
    position: absolute;
    width: 100%;
    height: 1000px;
    background:cyan ;
    justify-content: center;
    align-items: center;
}
.questionpanel
{
    height: fit-content;
    align-items: center;
    background: #3594cc;
    color: #fff;
    border: 4px solid rebeccapurple;
    box-shadow: 1px solid white;
    border-radius: 22px;
    text-align: center;
    text-decoration: underline;
    padding: 18px;
    margin: 14px;
    
}
.questionpanel p{
    font-size: large;
}
.headpanel{
    position: relative;
    display: flex;
    font-size: 13px;
    margin-left: 21px;
    font-variant: petite-caps;
}
.spantext{
    display: flex;
    margin: 8px 21px 0px 21px;
    justify-content: space-between;
    font-weight: bold;

}
.anspart{
    position: relative;
    width: 100%;
    height: 72vh;
    overflow-y: scroll;
    background: #dedcf5;
    color: black;
    margin: 1px;
    padding: 40px;
    font-size: medium;
}
.anspart li{
    text-decoration: none;
    list-style: circle;
}
.anspart li:hover{
    cursor: pointer;
    padding: 8px;
    background: linear-gradient(45deg, #d1f5ff, transparent);
}
.anspart li:nth-child(odd){
    background: linear-gradient(360deg, violet , #fff);
}
.srch{
    padding: 6px;
    background: darkcyan;
    border-radius: 18px;
}
.srch select{
    width: 200px;
    margin: 0 24px 3px 41px;
}
.srch button{
    width: 72px;
    padding: 5px;
    background: rgb(39, 121, 230);
    color: #fff;
    border: 1px solid black;
    border-radius: 5px;
}
.anspart li{
    list-style: disc;
    margin: 4px;
    padding: 3px;
    border: 2px solid;
    border-radius: 21px;
}
.ansinput{
    margin: 0px;
    display: flex;
    flex-direction: column;
    font-size: large;
    background: repeating-linear-gradient(45deg, #4d5451, transparent 100px);
    padding: 8px;
    align-items: center;
    justify-content: center;

}
.ansinput label{
    margin: 2px;
    font-size: medium;
}
.ansinput input{
    height: 46px;
    font-size: 14px;
    border: 3px solid;
    border-radius: 19px;
    width: 70%;
}
.ansinput input:nth-child(6){
    height: 73px;
}
.bloglist{
    padding: 12px;
    overflow-y: auto;
    background: aliceblue;
}
.bloglist ul a li{
    border-bottom: solid;
}
.resbtn{
    width: fit-content;
    height: 34px;
    background: #1cc7b0;
    font-size: 16px;
    margin: 5px;
    padding: 2px;
    font-weight: 600;
}
.quepart{
    padding: 12px;
    display: flex;
    flex-flow: column;
    background: antiquewhite;
    justify-content: center;
    align-items: inherit;
    border: 5px solid violet
}
.queinp input{
    width: 100%;
    padding: 3px;
    margin: 10px;
    height: 59px;
    border: 2px solid black;
}
.postbtn{
    margin: auto;
    width: 72px;
    padding: 5px;
    background: blue;
    color: #fff;
    border: 1px solid black;
    border-radius: 5px;
}