.container{
    background: #003147;
    width: fit-content;
    position: relative;
    width: fit-content;
    height: max-content;
    margin: auto;
    border: 1px solid black;
    border-radius: 10px;
}
.header{
    position: relative;
    display: flex;
    margin: 5px;
    justify-content: space-between;
    color: #fff;

}
.headingmain{
    font-family: "Serif", "Georgia";
    background: aliceblue;
    align-self: center;
    color: #1d02b3;
    margin-bottom: 0;
    border-radius: 10px;
    white-space: nowrap;
}
.content{
    background: rgb(143, 203, 238);
    display: flex;
    /* grid-template-columns: 1fr 1fr; */
    margin: 2px;
    padding: 2px;
    grid-gap: 5px;
    border-radius: 15px;

}
.timespan{
    background: linear-gradient(45deg, green, white);
    border-radius: 15px;
}
.details{
    background: linear-gradient(308deg, blue, white);
    border-radius: 15px;
}
.inpbox{
    position: relative;
    width: 70%;
}
.spanBox{
    margin: 2px;
    background: linear-gradient(45deg, rgb(221, 65, 208) white);
}
.btnbox{
    position: relative;
    display: flex;
    text-align: center;
}
.btn{
    border: 2px solid #000;
    background: palevioletred;
    border-radius: 5px;
    margin: 2px;
    padding: 2px;
    color: #fff;
    width: 100%;
    height: 40px;

}
.inp{
    display: grid;
    grid-template-columns: 150px 1fr;
}
.contpdf{
    display: grid;
    grid-auto-flow: column;
    grid-gap: 27px;
    border: 2px solid;
    font-size: 12px;
    margin: 10px;
    padding: 10px;
}
.ulcont{
    text-align: left;
    margin: 8px;
    font-size: 10px;
}
.ulcont ul{
    margin-left: 8px;
}
.ulcont ul li{
    list-style: disc;
    padding: 3px;
}
