  .bg{
    background: linear-gradient(#beffba, #66d2cd);
    color: #fff;
    width: 100%;
}
.top{
  display: flex;
  flex-direction: row;
  border: 1px solid rebeccapurple;
  border-radius: 20px;
}
.input{
  width: 44%;
  padding: 17px;
  margin: 16px;
  border: 2px solid #842c2c;
  background: linear-gradient(
45deg
, #3bffc9, #254ca5f7);
  border-radius: 47px;
}
  .row_doctors{
    display: flex;
    flex-wrap: wrap;
    /* overflow-x: scroll; */
    padding: 20px;
    max-height: 420px;
    background: linear-gradient(#dc72ff, #ffdfdf);
    overflow-y: scroll;
    border: 9px solid #10736a;
    border-radius: 29px;
    margin: 10px 26px;
  }
  .row_doctors::-webkit-scrollbar-{
    display: none;
  }
  .row_doctor{
    object-fit: contain;
    /* max-height: 284px; */
    width: 100%;
    max-width: 224px;
    margin: 7px;
    border: 1px solid #000;
    border-radius: 23px;
    transition: transform 450ms;
    overflow: visible;
    background: linear-gradient(#03a9f4, #e91ee3);
    padding: 13px 5px;
  }
  .row_doctor:hover{
    transform: scale(1.08);
  }
  .title{
    margin:5px;
    font-size: 16px;
  }
  .img{
    width: 97%;
    margin: 3px;
    padding: 6px;
 
  }
  .heading{
    color: #003147;
    font-size: 1.5rem;
    margin-top: 13px;
  }
  .item{
display: flex;
flex-direction: column;
  }
 .item .btn{
  font-size: 14px;
text-decoration: none;
text-align: center;
  margin: 4px 4px 0px 6px;
  padding: 0px 6px 8px 7px;
  color: #f4fffd;
  border: 1px solid;
  background: #104433;
}
.item .price{
  margin: -1px 4px 2px 12px;
    background: linear-gradient(
305deg
, #38a0f9, #3bd8c200);
    color: #fdfdff;
    font-size: 17px;
    /* width: -webkit-fill-available; */
    border: 1px solid #49f7af;
    border-radius: 5px;
    padding: 2px;
}
.desc{
  padding: 5px;
  font-size: 16px;
  font-weight: 400;
  color: #18ffca;
  margin: 2px 9px;
  border: 1px solid #f8f1ff;
  border-radius: 9px;
  width: fit-content;
  letter-spacing: 1px;
}
.uni{
  padding: 5px;
  font-size: 16px;
  font-weight: 400;
  color: #b6f5ff;
  margin: 2px 9px;
  border-bottom: 1px solid #f8f1ff;
  border-radius: 9px;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: 1px;
}
@media (max-width:608px){
  
.top{
  flex-direction: column;
}
.input{
  width: 90%;
    padding: 13px;
    margin: 12px;
    border: 2px solid #ffd714ad;
    background: linear-gradient( 
45deg
 , #3bffdb, #8ba525f7);
}
}