.container{
    position: relative;
    display: grid;
    grid-template-columns: 191px 280px;
    width: min-content;
    height: fit-content;
    /* background: rgb(236, 75, 75); */
    background:  rgb(175 26 26);
}
.title_text{
        font-size: small;
        font-weight: bold;
        color: #eee;
        white-space: nowrap;
}
.top{
    display: flex;
    width: 100%;
    margin: 5px;
    justify-content: flex-start;
}
.img{
    margin: 20px;
}
.right{
    display: flex;
    margin: 5px;
    padding-right: 22px;
    background: linear-gradient(355deg, #3aff3a, white);
    border:1px solid blue;
    border-radius: 10px;

}
/* .bottom{
    display: flex;
    justify-content: space-evenly;
    /* margin-left: 5px; 
} */
.btn{
    margin: 5px;
    padding: 2px;
    background: linear-gradient(45deg, blue, white);
    color: #000;
}
.btn:hover{
    cursor: pointer;
    padding: 4px;
    background: linear-gradient(45deg, rgb(234, 0, 255), white);
}
.feespan{
    border-right: 1px solid;
    padding: 3px;
    border-radius: 9px;
}
