.container{
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(183deg, #47ffb3, #e6e0f7);
    -webkit-clip-path: circle(1000px at 50% -48.5%);
    clip-path: circle(632px at 50% -40.5%);
    border: 2px solid #0c1d71;
    border-radius: 61px;
    border-style: dotted;
}
.card{
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: rgb(207, 0, 0);
    border-radius: 5px;
    box-shadow: 0 -15px 15px rgb(62 174 253 / 88%), inset 0px -15px 15px rgb(155 104 187 / 62%), 0 8px 13px rgb(133 40 191 / 81%), inset 0 0px 13px rgb(162 247 172 / 65%)
}
.card::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 270px;
    top: 0;
    background: linear-gradient(to top, #00f2fe, #e5edf3);
    clip-path: circle(400px at 50% -48.5%);
}
.main{
    width: 100%;
    background: linear-gradient(240deg, #003147, #b3277b);

}
.top{
    top: 19px;
    position: relative;
    margin: 30px;
    border-bottom: 4px solid #5628b1;
    border-radius: 30%;

}
.imgBox{
    position: relative;
    width: 129px;
    height: 129px;
    border: 2px solid #1451ff;
    border-radius: 50%;
    overflow: hidden;
    margin: auto;
}
.imgBox img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;

}
.nameplate{
    margin: auto;
    width: 50%;
    font-size: .7rem;
    text-align: center;
}
.grid{
    display: grid;
    grid-template-columns: 40% 40%;
    margin: 14px;
    padding: 32px;
    border: 4px solid #2ff2ff;
    border-radius: 40px;
    justify-content: space-evenly;
}
.grid form{
    height: fit-content;
}
.inpUser{
    margin: 10px;
    margin-bottom: 20px;
    padding: 0px;
    background: linear-gradient(
335deg
, #00f5c8, #f9ecec08);
}
.userinp{
    width: 100%;
    height: 40px;
    font-family: "Times New Roman", Times, serif;
    font-size: 16px;
    letter-spacing: 1px;
}
.testlist{
    position: relative;
    background: linear-gradient(
        16deg
        , #ff0357, #fff);
            justify-content: center;
            text-align: center;
            margin: 10px 20px 10px 20px;
            
}
.tableData{
    height: 260px;
    overflow-y: scroll;
    position: relative;
}
.table{
    font-size: 12px;
    width: inherit
    
}
.table th{
    padding: 0;
    border: 1px solid black;
    border-radius: 24px;

}
.table td{
    border: 1px solid blueviolet;
}
.table tr td{
    text-align: center;
    padding: 4px;
    width: 0px;
    position: relative;
    display: table-cell;
}
.table tr:nth-child(odd){
    border: 1px solid rgb(16, 235, 180);
    background: #272cc5fa;
    color: #fff;;
}
.table tr:nth-child(even){
    border: 1px solid rgb(16, 235, 180);
    background: rgb(151, 5, 235);
    color: #fff;;
}
.data{
    color: #fff;
}
.bookingmain{
    position: relative;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.bookingmid {
    position: relative;
    width: inherit;
    justify-content: center;
    border: 2px solid green;
    border-radius: 10px;

}
.acctogglebtn{
    text-align: center;
    background: linear-gradient(178deg, black, transparent);
    font-size: 30px;
    color: #fff;
    border: 1px solid;
    cursor: pointer;
}
    
@media (max-width:850px){
.grid{
    display: flex;
    flex-direction: column;
}
}