.footerbox{
    font-size: 14px;
    position:relative;
left:0;
bottom:0;
right:0;
}
.footer{
    background: linear-gradient(19deg, black, rgb(95 193 175 / 64%));
            color: #fff;
}
.footer_left{
    margin-right: 1.25em;
    margin-bottom: 2em;
}
.footer_left p{
    font-size: 12px;
    padding: 6px;
}

.footer h2{
    font-weight: 500;
    font-size: 17px;
    text-align: center;
    color: #00cbec;
    border: 1px solid;
    border-bottom: 5px solid;
    white-space: nowrap;

}
.footer ul{
    overflow-x: scroll;
    list-style: none;
    padding-left: 0;
    text-align: center;
    margin: 15px 0px 15px 0px;
    background: linear-gradient(45deg, black, transparent);

}
.footer li {
    line-height: 1.5em;
}
.footer li:hover {
    z-index: 100;
}
.socialmedia{
    list-style: none;
    display: flex;
    width: 25%;
    margin-left: 18px
}
.socialmedia li, .socialmedia a{
    margin: 0px 8px 0px 8px
}
.socialmedia li:hover, .socialmedia a:hover{
    padding: 2px;
}
.footer a{
    text-decoration: none;
    color: #fff;
    white-space: nowrap;
}
.footer a:hover{
    text-decoration: underline;
    font-size: 18px;
    padding: 6px;
    color: rgb(214, 241, 235);
}
.footer_right{
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.footer_right > *{
    flex: 1 50%;
    margin-right: 1.25em;
}
.card{
    width: 28rem;
    background: linear-gradient(45deg, #5438ef, #378c908a);
    color: #fff
}
.modal_dialog{
    width: 487px;
}
.inpform h5{
    color: #40fff6;
}
.inpform{
    padding: 8px;
}
.inpform input, .inpform select{
    width: 228px;
    height: 37px;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 34px 1px;
    border-bottom: 1px solid #003147;
    width: 100%;
    background: linear-gradient(#2626e0, #e03333 );
    border: 4px solid #303131;
    border-radius: 40px;
}
.cards .cardBox{
    margin: 30px 22px 18px 8px;
    padding: 0px 0px 0px 0px;
    border: 4px solid #003147;
    border-radius: 10px;
}
.footer h2{
font-size: 15px;
}

@media(max-width:468px){
    .footer_right{
        display: block;
    }
    .footer_left{
        font-size: 10px;
    }
}

