.chat_container{
    position: relative;
    /* height: 100vh; */
    width: 100%;
    /* min-width: 600px;
    max-width: 800px; */
    /* max-height: 800px; */
    height: 100vh;
    background: #fff;
    border-radius: 10px;
    margin: auto;
    display: grid;
    grid:   'search_container chat_title' 71px
            'conversation_list chat_message_list' 1fr
            'new_message_container chat_form' 78px
            /40% 60%;
    place-items: center;
}
.chat_container.activeChat{
    grid:   'search_container chat_title' 71px
    'conversation_list chat_message_list' 1fr
    'new_message_container chat_form' 78px
    /0% 100%;
}
.search_container, .conversation_list, .new_message_container{
background: #724e66;
}
.search_container{
    display: flex;
    align-items: center;
    grid-area: search_container;
    border-radius: 10px 0 0;
    padding: 0px 20px;
    box-shadow: 0 1px 3px -1px, rgba(0, 0, 0, 0.75);
    z-index: 1;
    background: rgb(238, 222, 222);
    width: 100%;
    height: 100%;
}
.search_container input{
    color: #000;
    width: 100%;
    height: 40px;
    font-weight: 400 ;
    padding-left: 40px;
    outline: none;
    border-radius: 15px;
    /* padding-left: 0px; */
    padding-right: 0px;
    font-size: 1rem;
    background: rgba(255, 255, 255, 0.3);
    text-align: start;
}
.search_container.activeChat input{
    display: none;
}
