.search_container input::before{
color: #ddd;
font-weight: bold;

}
.conversation_list{
    grid-area: conversation_list;
    width: 100%;
    height: 100%;
    background: silver;
    overflow-y: scroll;
}
.prepbar{
    display: none;
}
.toggleprep{
    display: block;
    width: fit-content;
    position: absolute;
    bottom: 73px
}
.fileinp{
    display: none;
}
.msgimg{
    height: 120px;
    width: 120px;
}
.msgimg:hover{
    height:300px;
    width:300px;
    cursor: pointer;
}
.upbtn{
    bottom: -5px;
    left: 186px;
    /* right: 2px; */
    width: 75px;
    font-size: 8px;
    background: magenta;
}
.activeimg{
    display: block;
    position: absolute;
}
.imginp{
    position: absolute;
    width: 10rem;
    background: greenyellow;
    left: 145px;
}
.btn{
    width: 97px;
    height: fit-content;
    background: #2ff6ff;
    color: #000;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.5px;
    padding: 2px;
    border: 1px solid;
    border-radius: 11px;
}
.conversation{
    display: grid;
    grid-template-columns: 3fr 1fr max-content;
    grid-gap: 1px;
    color: #ddd;
    font-size: 1.3rem;
    border-bottom: 2px solid #000;
    padding: 8px 0px 1px 0px;
    background: linear-gradient(8deg, #2317e4, transparent);
    
}
.conversation > .img{
    grid-row: span 2;
    height: 40px;
}
.conversation.active,
.conversation:hover{
background: #002c88;
}
.conversation:hover{
    cursor: pointer;
    height: 50px;
    
}
.title_text{
    font-size: small;
    font-weight: bold;
    color: #eee;
    white-space: nowrap;
    padding-left: 5px;
    overflow-x: hidden;
    /* text-overflow: ellipsis; */
}
.delete_icon{
    position: absolute;
    background: pink;
    color: #fff;
float: right;
margin: 10px;
width: 20px;
height: 20px;
}
.created_date{
color: #ddd;
font-size: 0.7rem;
}
.conversation_message{
    grid-column: span 2;
    white-space: nowrap;
    padding-left: 5px;
    overflow-x: hidden;
    text-overflow: ellipsis;
    font-size: 1rem;
}

.new_message_container{
    display: flex;
    /* grid: 40px / max-content; */
    grid-area: new_message_container;
    align-content: center;
    justify-content: space-between;
    border-top: 1px solid rgba(0, 0, 0, 0.25);
    border-radius: 0 0 0 10px;
    width: 100%;
    height: 100%;
    background: rgb(0, 4, 255);
    border-top: 1px solid rgb(0, 0, 0, 0.25);
    padding: 0 15px;

}
.new_message_container .link{
    place-content: center center;
    text-align: center;
    background: #eee;
    border-radius: 100%;
    color: #9f89eb;
    text-decoration: none;
    font-size: 2rem;
}

.chat_title, .chat_form{
background: rgb(243, 147, 147);

}
.chat_title{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    grid-area: chat_title;
    background: skyblue;
    font-size: 2rem;
    border-radius: 0 10px 0 0;
    box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.75);
}
.title{
    font-size: 1rem;
    margin-left: 60px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    letter-spacing: 1px;
}
.chat_message_list{
    width: 100%;
    height: 100%;
    grid-area: chat_message_list;
    background: rgb(231, 205, 170);
    padding: 0 20px;
    overflow-y: scroll;
}
.message_row{
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: 70%;
}
.message_row .img{
    grid-row: span 2;
}
.message_content{
    display: grid;
}
.msg_text{
    padding: 5px 3px;
    font-size: 1rem;
    margin-bottom: 0px;
}
.msg_time{
    font-size: 0.7rem;
    color: #777;
}
.sender .message_content{
    justify-items: end;
}
.sender{
    justify-content: end;
    justify-items: end;
}
/* .receiver .message_content{
    grid-template-columns: 30px 1fr;
    grid-column-gap:15px ;
    //set when avatar to show
} */
.receiver{
    justify-items: start;
}
.sender .msg_text{
    background: linear-gradient(337deg, #1ed01e, white);
    border: 1px solid #264cf1;
    padding: 8px;
    border-radius: 19px 19px 0px 19px;
    width: fit-content;
}
.receiver .msg_text{
    background: linear-gradient(45deg, #1eafd0, white);
    border: 1px solid #264cf1;
    padding: 8px;
    border-radius: 19px 19px 19px 0;
    width: fit-content;
}
.chat_form{
    display: grid;
    grid: 51px / 1fr 40px;
    grid-gap: 15px;
    grid-area: chat_form;
    width: 100%;
    height: 100%;
    background: rgb(38, 212, 175);
    border-radius: 0 0 10px 0;
    box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.75);
    padding-left: 12px;
    padding-right: 22px;
    align-content: center;
}
.chat_form input{
    width: 100%;
    outline: none;
    padding: 0px;
    border: 2px solid black;
    border-radius: 20px;
    font-size: 1rem;
    font-style: italic;
    height: 40px;
    margin: auto;
}
.btnsub{
    width: 50px;
    height: 30px;
    margin: 5px;
    font-size: 0.7rem;
    text-align: center;
    background: linear-gradient(45deg , blue, white);
    border: 1px solid #000;
    color: #000;
}
::-webkit-scrollbar{
    display: none;
}
