.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 0px 23px;
    padding: 34px 1px;
    border-bottom: 1px solid #003147;
    width: 100%;
    background: linear-gradient(#2626e0, #e03333 );
    border: 4px solid #303131;
    border-radius: 40px;
}
.cards .cardBox{
    margin: 30px 22px 18px 8px;
    padding: 0px 0px 0px 0px;
    border: 4px solid #003147;
    border-radius: 10px;
}
