.main{
    position: absolute;
    width: max-content;
    height: fit-content;
    background: repeating-linear-gradient(      45deg
        , #1d87e2, transparent 52px)
}
.header{
    display: flex;
    justify-content: space-between;
    width: 185px;
}
.heading{
    text-align: center;
    background: #1c90f5;
    font-size: 15px;
    color: #fff;
}
.heading fa{
    margin: 2px;
}
.reqbox{
    display: flex;
    justify-content: space-between;
    border: 2px solid #000;
    border-radius: 19px;
    margin-top: 5px;
}
.left{
    position: relative;
    float: left;
    margin: 0px;
    padding: 5px;
}
.right{
    display: flex;
    flex-direction: column;
    background: linear-gradient(45deg, #e634c2f0, transparent);
}
.btn{
    height: 33px;
    margin: 8px;
    background: #10ff00;
    font-size: 16px;
    font-weight: 500;
    padding: 3px;
    text-align: center;
}
.btn:hover{
    padding: 5px;
    background: #94fff0;
    cursor: pointer;
}
.profilebtn{
    margin-left: 14px;
    padding: 1px 7px 0px 7px;
    background: #0089ff;
    color: #fff;
    font-size: 15px;
    border: 1px solid #362fdc;
    text-decoration: none;

}
.profilebtn:hover{
    padding: 5px;
    background: #94fff0;
    color: #000;
    cursor: pointer;
}
.active{
    display: none;
}