.prescription{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: linear-gradient(45deg , #c1ffe8, #62efbb);;
    margin: 5px;
    padding: 10px;
    justify-content: center;
    align-items: center;
    overflow-x: auto;
    transform: 0.6s;

}
.preslist{
    position: relative;
    width: fit-content;
}
.namelbl span{
    font-size: 20px;
    width: fit-content;
    background: #ec5571;
    color: white;
    border: 1px solid;
    padding: 5px;
    border-radius: 11px
}
.tableData{
    height: 330px;
    overflow-y: scroll;
}

.table{
    overflow-x: auto;
    width: 100%;

    transform: 0.6s;
    
}
table tbody {
    display: block;
    max-height: 300px;
    overflow-y: scroll;
    text-align: center;
  }
  
  table thead, table tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
    font-size: 12px;
    text-align: center;
    white-space: wrap;
    overflow-wrap:break-word;

  }
