.table td{
    border: 1px solid blueviolet;
}
.table tr td{
    text-align: center;
    padding: 7px;
    border-right-width: medium;
    font-size: 12px;
    
    transform: 0.6s;
}
.table tr:nth-child(odd){
    border: 1px solid rgb(16, 235, 180);
    background: #a527c5fa;
    color: #fff;
}
.table tr:nth-child(even){
    border: 1px solid rgb(16, 235, 180);
    background: linear-gradient(360deg ,#0387ff, #fff);
    color: #000;
}
.addbtn{
    background: crimson;
    margin: auto;
    height: 39px;
    font-size: 23px;
    color: #fff;
    border-radius: 16px
}
.upbtn{
    background: indigo;
    color: #fff;
    height: 39px;
    width: 78px;
    border-radius: 17px
}
