.header{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 74px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: 0.6s;
    padding: 14px 65px;
    z-index: 100;
    background: linear-gradient(
0deg
, #00b9ff,#ff2ef9cc);
    border: 2px solid #136798;
    margin-top: 1px;
    border-radius: 43px;
    vertical-align: middle;
}
.header .logo{
    position: relative;
    font-weight: 700;
    color: #1d02b3;
    margin-left: 9px;
    text-decoration: none;
    font-size: 2em;
    text-transform: uppercase;
    border-radius: 2px solid rgb(224, 224, 224);
    border-radius: 20%;
    border-style: dotted;
    padding: 3px;
    font-family: "Serif", "Georgia";
    background: aliceblue;
}
.header ul{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header ul li{
    position: relative;
    list-style: none;
}
.header ul li .link{
    position: relative;
    margin: 0 15px;
    text-decoration: none;
    color: blue;
    letter-spacing: 2px;
    font-weight: 500;
    transition: 0.5s;
}
.header ul li .link{
    color: #000;
}
.sticky{
    position: sticky;
    width: 100%;
    top: 0;
    z-index: 100;
}
.header ul li .link:hover{
    padding:5px;
    color: rgb(232, 231, 233);
    border-bottom: 2px solid black;
    border-radius: 35%;
    transition: 0.5s;
    font-size: 1.2em;
}
.toggle
{
    position: relative;
    width: 60px;
    height: 60px;
    cursor: pointer;
}
.toggle::before{
    content: '\f0c9';
    font-family: fontAwesome;
    position: absolute;
    width: 100%;
    height: 100%;
    line-height: 60px;
    font-size: 24px;
    text-align: center;
    color: #000;

}
.toggleNav{
    display: none;
}
.toggleBtn{
    position: fixed;
    height: 25px;
    top: 1px;
    z-index: 500;
    left: 38px;
}
@media (max-width : 700px){
    .logo{
        display: none;
    }
    .header{
        flex-direction: row;
        height: 50%;
        /* flex-wrap: wrap; */
    }
    .header ul{
        display: flex;
        flex-wrap: wrap;
    align-items: center;
    }
}