*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'poppins', sans-serif;
    }
.body{
    background: lightblue;
    /* min-height: 1366px; */
}
#root1{
    position: relative;
    /* min-height: 919px; */
    background: #fff;
    /* display: grid; */
    grid-template-columns: 1fr 3fr;
    box-shadow: 0 35px 55px rgb(0 0 0 / 10%);
    width: 100%;
}
.left_Side{
    position: relative;
    background: #003147;
    padding: 13px;
    display: none;
    height: 150vh;
    /* max-height: 150vh; */
    overflow-y: auto;
}
.profileText{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 1px solid #fff;
}
.profileText .imgBox
{
    position: relative;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
}
.profileText .imgBox img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.profileText h2{
    color: #fff;
    font-size: 1em;
    margin: 2px 8px;
    text-transform: uppercase;
    text-align: center;
    font-weight: 600;
    line-height: 1.4em;
    word-wrap: normal
}
.profileText h2 span{
    margin: 36px;

}
.links{
    width: 100%;
    top: 19px;
    border-top: 1px solid #fff;
    margin: 25px 0px;
    list-style: none;
}
.links li{
    position: relative;
    width: 100%;
    list-style: none;
    border-bottom: 1px solid #fff;
    margin: 17px 0px;
    padding: 10px 15px;
}
.links li:hover{
    background-color: #03a9a4;
}
.links li a{
    display: block;
    width: 100%;
    display: flex;
    text-decoration: none;
    color: #fff;
}
.links li a .icon{
    position: relative;
    display: block;
    min-width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
}
.links li a .icon i{
    color: #fff;
    font-size: 24px;
}
.links li a .title{
    position: relative;
    display: block;
    padding: 0 10px;
    height: 60px;
    line-height: 60px;
    white-space: nowrap;
}
.right_Side{
    position: relative;
    background: #fff;
    width: 100%;
    top: 0;
    padding-top: 0;
    overflow-y: auto;
    background: aqua;
    min-height: 100vh;
    margin-top: 5px;
}
.right_Side .topbar{
    background: #7dcbd8;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 3px solid #003147;
    border-radius: 16px;
}
.right_Side.active{
    position: absolute;
    left: 371px;
    height: 100vh;
}
.left_Side.active{
    display: block;
    width: 369px;
    height: 100vh;
}
.homefooter .footer_right{
   flex-flow: wrap;
}
.toggle
{
    position: relative;
    width: 60px;
    height: 60px;
    cursor: pointer;
}
.toggle::before{
    content: '\f0c9';
    font-family: fontAwesome;
    position: absolute;
    width: 100%;
    height: 100%;
    line-height: 60px;
    font-size: 24px;
    text-align: center;
    color: #000;

}


.search{
    position: relative;
    width: 400px;
    margin: 0 10px;
}
.search label{
    position: relative;
    width: 100%;
}
.search label input{
    width: 100%;
    height: 40px;
    border-radius: 40px;
    padding: 5px 20px;
    padding-left: 35px;
    outline: none;
    border: 1px solid rgba(0,0,0,0.2);
    font-size: 20px;
}
.search label #fa{
    position: absolute;
    left: 15px;
    top: 4px;
}
#fatimes{
position: relative;
    top: -30px;
    float: right;
    /* display: contents; */
    width: 41px;
    height: 5px;
}
#fatimes:hover{
    cursor: pointer;
}
.user{
    position: relative;
    min-width: 50px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    cursor: pointer;
}
.user img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.Accordian{
    background: darkcyan;
    border: 2px solid black;
    border-radius: 15px;
    margin: 16px
}

@media (max-width:400px){
    .left_Side.active{
     width: 311px;
    }
    .right_Side.active{
     left: 313px;
    }
 }
@media (max-width:809px){
    .contactInfo h2{
        font-size: 10px;
    }
    .left_Side.active .contactInfo ul{
       margin: 10px;
    }
}

