.cards{
    display: flex;
    justify-content: space-between;
    margin: 15px 0px;
    padding: 34px 1px;
    border-bottom: 1px solid #003147;
    width: 102%;
}
.cards .cardBox{
    width: 30%;
    margin: 30px 22px 18px 8px;
    padding: 0px 0px 0px 0px;
    border: 4px solid #003147;
    border-radius: 10px;
}
.clockside{
    justify-content: center;
    align-items: center;
    background: linear-gradient( 
45deg
 , #35f6ff, #ff3f3fd9);
    width: max-content;
    /* left: 90%; */
    float: right;
    z-index: 50;
    top: 0px;
    right: 151px;
    position: absolute;
}
.reqlist{
    position: absolute;
    float: right;
    right: 235px;
    z-index: 100;

}

